import React, { useEffect, useState } from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import './App.css';
import Header from './components/Header';
import ImageCarousel from './components/ImageCarousel';
import Footer from './components/Footer';
import { motion, useAnimation } from 'framer-motion';
import MainImage from './components/MainImage';
import backgroundImg from './assets/TadpoleDiary_background.png';
import DownloadButtons from "./components/DownloadButtons";
import ImageList from "./components/ImageList"; //  import
import Introduction from './components/Introduction'; // Introduction  import

function App() {
    // Framer Motion   
    const controls = useAnimation();
    //     state
    const [section, setSection] = useState(0);
    //       state
    const [scrolling, setScrolling] = useState(false);

    //    
    const handleScroll = (direction) => {
        if (!scrolling) {
            setScrolling(true);
            setSection((prevSection) => {
                //   ,  0 2   
                const nextSection = Math.max(0, Math.min(prevSection + direction, 2));
                // Framer Motion   
                controls.start({
                    y: `-${nextSection * 100}vh`,
                    transition: { duration: 0.5, ease: 'easeInOut' }, //    easeInOut 
                });
                setTimeout(() => setScrolling(false), 1000); //    1    
                return nextSection;
            });
        }
    };

    useEffect(() => {
        //    
        const onWheel = (e) => {
            e.preventDefault(); //    
            if (e.deltaY > 0) {
                handleScroll(1); //  
            } else if (e.deltaY < 0) {
                handleScroll(-1); //  
            }
        };
        window.addEventListener('wheel', onWheel, { passive: false }); // passive: false  preventDefault() 

        return () => window.removeEventListener('wheel', onWheel); //      
    }, [scrolling]); // scrolling state   useEffect   


    return (
        <div className="App">
            <Header />
            {/*   */}
            <motion.div
                animate={controls} //    
                initial={{ y: '0vh' }} //   
                style={{ height: '100vh', backgroundImage: `url(${backgroundImg})`, backgroundSize: 'cover' }} //   
            >
                {/*    */}
                <div className="section" style={{ height: '100vh', display: 'flex', flexDirection: 'column', justifyContent: 'center', alignItems: 'center', backgroundColor: 'transparent' }}>
                    <MainImage />
                    <DownloadButtons />
                </div>
                {/*    */}
                <div className="section" style={{ height: '100vh', display: 'flex', flexDirection: 'column', justifyContent: 'center', alignItems: 'center', backgroundColor: '#6BD2BB' }}>
                    <ImageList />
                </div>
                {/*    */}
                <div className="section" style={{
                    height: '100vh',
                    display: 'flex',
                    flexDirection: 'row', /*    */
                    backgroundColor: '#ADD8E6'
                }}>
                    <Introduction />
                    <div className="section-right" style={{
                        flex: 1.2, /*      */
                        display: 'flex',
                        justifyContent: 'center',
                        alignItems: 'center',
                    }}>
                        <ImageCarousel />
                    </div>
                </div>
            </motion.div>
            <Footer />
        </div>
    );
}

export default App;
