import React from 'react';
import { Carousel } from 'react-bootstrap';
import gameImage1 from '../assets/05.png';
import gameImage2 from '../assets/06.png';
import gameImage3 from '../assets/07.png';
import gameImage4 from '../assets/08.png';
import gameImage5 from '../assets/09.png';

const images = [gameImage1, gameImage2, gameImage3, gameImage4, gameImage5];

function ImageCarousel() {
    return (
        <div className="carousel-container">
            <Carousel interval={null} style={{ justifyContent: 'flex-start' }}>
                {images.map((image, index) => (
                    <Carousel.Item key={index}>
                        <img className="d-block mx-auto carousel-image" src={image} alt={`Slide ${index + 1}`} />
                    </Carousel.Item>
                ))}
            </Carousel>
            {/*     CSS */}
            <style>
                {`
                    .carousel-container {
                        position: relative;
                        height: 100%;
                        width: 100%;
                        max-width: 600px; /*   */
                        margin: 0 auto;
                        display: flex;
                    }
                    .carousel-image {
                        width:80%; /*   60%  */
                        height: auto;
                    }
                    .carousel-control-prev, .carousel-control-next {
                        position: absolute;
                        top: 50%;
                        transform: translateY(-50%);
                        background-color: rgba(0, 128, 0, 0.5);
                        color: white;
                        width: 50px; /*   */
                        height: 50px; /*   */
                        display: flex;
                        justify-content: center;
                        align-items: center;
                        cursor: pointer;
                    }
                    .carousel-control-prev {
                        left: -50px; /*      */
                    }
                    .carousel-control-next {
                        right: -50px; /*      */
                    }

                    /*   */
                    @media (max-width: 1920px) {
                        .carousel-container {
                            max-width: 800px;
                        }
                        .carousel-image {
                            width: 50%;
                        }
                    }

                    @media (max-width: 1200px) {
                        .carousel-container {
                            max-width: 500px;
                        }
                        .carousel-image {
                            width: 70%;
                        }
                    }

                    @media (max-width: 992px) {
                        .carousel-container {
                            max-width: 400px;
                        }
                        .carousel-image {
                            width: 85%;
                        }
                    }

                    @media (max-width: 768px) {
                        .carousel-container {
                            max-width: 300px;
                        }
                        .carousel-image {
                            width: 90%;
                        }
                    }

                    @media (max-width: 576px) {
                        .carousel-container {
                            max-width: 200px;
                        }
                        .carousel-image {
                            width: 95%;
                        }
                        .carousel-control-prev, .carousel-control-next {
                            width: 30px; /*       */
                            height: 30px; /*       */
                        }
                    }
                `}
            </style>
        </div>
    );
}

export default ImageCarousel;
