import React from 'react';
import './ImageList.css';

import tadImage1 from '../assets/tad_intro1.png';
import tadImage2 from '../assets/tad_intro2.png';
import tadImage3 from '../assets/tad_intro3.png';

const images = [tadImage1, tadImage2, tadImage3];

const ImageList = () => {
    return (
        <div className="image-list-container">
            {images.map((image, index) => (
                <div key={index} className="image-list-item">
                    <img src={image} alt={`Image ${index + 1}`} className="image-list-image" />
                </div>
            ))}
        </div>
    );
};

export default ImageList;
