import React from 'react';
import {Carousel, Col, Row} from 'react-bootstrap';
import googleplay from "../assets/googleplay.png";
import appstore from "../assets/appstore.png";

function DownloadButtons() {

    const googleButtonClick = () => {
        window.open('https://play.google.com/store/apps/details?id=com.neeznggnz.tadpolediary&hl=en_US', '_blank');
    };

    const appleButtonClick = () => {
        window.open('https://apps.apple.com/kr/app/tadpole-diary/id1668259802', '_blank');
    };

    return (
        <Row className="mt-4">
            <Col className="d-flex justify-content-end">
                <img
                    src={googleplay}
                    alt="Google Play"
                    style={{cursor: 'pointer', width: '300px', height: 'auto'}}
                    onClick={googleButtonClick}
                />
            </Col>
            <Col className="d-flex justify-content-start">
                <img
                    src={appstore}
                    alt="App Store"
                    style={{cursor: 'pointer', width: '300px', height: '91px', marginTop: '2px'}}
                    onClick={appleButtonClick}
                />
            </Col>
        </Row>
    );
}

export default DownloadButtons;
